body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 2rem;
}

.formulario input{
  width: 100%;
  padding: 5px;
  color: gray;
}
.resultado-container {
  display: flex;
  flex-wrap: wrap;
}
.card-personaje {
  margin: 10px 10px;
  padding: 10px;
  border-radius: 12px;
  box-shadow: 0px 3px 16px 0px rgba(217,217,217,1);
}